//@ts-ignore
import api from "npm-api";
import React, { useEffect } from "react";

const npm = new api();

interface PackageProps {
    name: string;
}

function Package({ name }: PackageProps) {
    const repo = npm.repo(name);

    useEffect(() => {
        (async () => {
            const pkg = await repo.package();
            console.log(pkg.description);
        })();
    });

    return (
        <div className="package">
            <a
                href={`https://www.npmjs.com/package/@aeroware/${name}`}
                target="_blank"
                rel="noreferrer"
            >
                <h3 className="title">
                    <span className="red">@aeroware/</span>
                    {name}
                </h3>
            </a>
            <img src={`https://img.shields.io/npm/dm/@aeroware/${name}.svg`} alt="" />
            <img src={`https://img.shields.io/npm/dt/@aeroware/${name}.svg`} alt="" />
            <img src={`https://img.shields.io/npm/v/@aeroware/${name}.svg`} alt="" />
        </div>
    );
}

export default Package;
